import mailSender from "../apis/mailSender";
import firebase from "../apis/firebase";
import {
  CHANGE_BACKGROUND,
  SEND_CONTACT,
  FETCH_SONGS,
  FETCH_SONG,
  CREATE_SONG,
  EDIT_SONG,
  DELETE_SONG
} from "./types";
import history from "../history";

import f01 from "../assets/img/f01.jpg";
import f01x from "../assets/img/f01x.jpg";
import f02 from "../assets/img/f02.jpg";
import f02x from "../assets/img/f02x.jpg";
import f03 from "../assets/img/f03.jpg";
import f03x from "../assets/img/f03x.jpg";

export const changeBackground = page => {
  let image = "";
  let width = window.innerWidth;
  if (page === "index") {
    image = width >= 576 ? f01x : f01;
  }

  if (page === "about") {
    image = width >= 576 ? f02x : f02;
  }

  if (page === "portfolio") {
    image = width >= 576 ? f03x : f03;
  }

  if (page === "equipment") {
    image = width >= 576 ? f01x : f01;
  }

  if (page === "contact") {
    image = width >= 576 ? f02x : f02;
  }
  return {
    type: CHANGE_BACKGROUND,
    payload: image
  };
};

export const formSender = formValues => async dispatch => {
  const response = await mailSender.post("", formValues);
  dispatch({ type: SEND_CONTACT, payload: response.ok });
};

export const fetchSongs = () => async dispatch => {
  const response = await firebase.get("/catalogos.json");
  dispatch({ type: FETCH_SONGS, payload: response.data });
};

export const fetchSong = idCancion => async dispatch => {
  const response = await firebase.get(`/catalogos/${idCancion}.json`);
  dispatch({ type: FETCH_SONG, payload: response.data });
};

export const createSong = formValues => async dispatch => {
  formValues.id = Math.random()
    .toString(36)
    .substr(2, 9);
  const idCancion = formValues.id;
  const response = await firebase.put(
    `/catalogos/${idCancion}.json`,
    formValues
  );
  dispatch({ type: CREATE_SONG, payload: response.data });
  history.push("/song/list");
};

export const editSong = formValues => async dispatch => {
  const idCancion = formValues.id;
  const response = await firebase.patch(
    `/catalogos/${idCancion}.json`,
    formValues
  );
  dispatch({ type: EDIT_SONG, payload: response.data });
  history.push("/song/list");
};

export const deleteSong = idCancion => async dispatch => {
  await firebase.delete(`/catalogos/${idCancion}.json`);
  dispatch({ type: DELETE_SONG, payload: idCancion });
  history.push("/song/list");
};
